import { useMemo, useEffect, useState } from 'react';
import { getAirdrops, AirdropInfo } from 'src/utils/airdrop';
import { useActiveWeb3React } from './useActiveWeb3React';
import { contractConfig } from 'src/config';
import { getTBTCChefContract } from 'src/utils/contract';
import { useAsync, useAsyncCallback } from 'react-async-hook';
import { BigNumber } from 'ethers';

// TODO: mock here

export function useAirDropState() {
  const { account, library } = useActiveWeb3React();
  const [txHappend, setTxHappend] = useState(0);

  const airdropInfo = useMemo(() => {
    return getAirdrops(account);
  }, [account]);

  const TBTCChefContract = useMemo(() => {
    return getTBTCChefContract(contractConfig.TBTC_CHEF, library, account);
  }, [account, library]);

  const pendingAirdrops = useAsync<BigNumber>(() => {
    if (!airdropInfo) {
      return undefined;
    }
    return TBTCChefContract.pendingAirdrop(airdropInfo.index, account, airdropInfo.fullAmount, airdropInfo.proof);
  }, [
    airdropInfo,
    TBTCChefContract,
    account,
    txHappend
  ]);

  const asyncClaimClick = useAsyncCallback(
    async () => {
      if (!airdropInfo) {
        return;
      }
      const tx = await TBTCChefContract.claim(airdropInfo.index, account, airdropInfo.fullAmount, pendingAirdrops.result, airdropInfo.proof);

      if (tx) {
        await tx.wait();
        setTxHappend(t => t + 1);
      }
    }
  );

  return {
    airdropInfo,
    pendingAirdrops,
    asyncClaimClick
  };
}
