import { utils, BigNumber } from 'ethers';
import { parseBalance } from 'src/utils/format';
import { Token } from 'src/types/token';
import { TBTC } from 'src/constants/token';
import airdrops from 'src/constants/airdrop.json';

export type AirdropType = 'BTCSTHolder' | 'BTCSTBNBLPHolder' | 'BTCSTFound';

export enum AirdropReason {
  BTCSTHolder = 'BTCSTHolder',
  BTCSTBNBLPHolder = 'BTCSTBNBLPHolder',
  BTCSTFound = 'BTCSTFound',
  BTCSTTreasury = 'BTCSTFound'
}

export type AirdropInfo = {
  token: Token;
  index: BigNumber;
  fullAmount: BigNumber;
  proof: string[];
  flags: {
    [key: string]: boolean;
  };
  details: {
    [key in AirdropType]: string;
  };
};

