export function getAirdrops(account: string) {
  const data = airdrops.claims[account];
  if (!data) {
    return null;
  }

  const airdrop: AirdropInfo = { 
    token: TBTC,
    index: BigNumber.from(data.index),
    fullAmount: BigNumber.from(data.amount),
    proof: data.proof,
    flags: data.flags,
    details: data.details
  }

  return airdrop;
}
